import { css } from 'lit';

export const styles = css`[part~=base]:hover [part=control]{background:hsla(var(--igc-gray-600), 0.06)}[part="base checked"]:hover [part~=control]{background:hsla(var(--igc-secondary-500), 0.06)}[part="base checked"]:focus-within [part~=control]{background:hsla(var(--igc-secondary-500), 0.12)}:host(:not([disabled])[invalid]) [part="control checked"]::before{background:hsla(var(--igc-error-500), var(--igc-error-a))}`;
